:root{
  --header-height:400px;
  --header-width:100%;
}

header{
  width: var(--header-width);
  height: var(--header-height);

  .headerImg{
    position: absolute;
    width: var(--header-width);
    height: var(--header-height);
    background-image: url("../../resource/sample.png");
    background-size: cover;
    background-position: center;
    filter: blur(10px);
  }

  .headerTextBox{
    position: relative;
    top: 150px;
    text-align: center;
    color: #000;
    h1{
      position: relative;
      animation-name: leftToright;
      animation-duration: 1s;
    }
    h2{
      opacity: 0;
      position: relative;
      animation-delay: 0.1s;
      animation-name: leftToright;
      animation-duration: 1s;
      animation-fill-mode: forwards;
      b{
        font-weight: 300;
        color: #000;
        animation-name: headerTextGradient;
        animation-duration: 10s;
        animation-delay: 0.9s;
        animation-iteration-count: infinite;
      }
    }
  }

}

