.topMenu {
  position: fixed;
  z-index: 99;
  width: 100%;
  height: 70px;

  div{
    max-width: 1200px;
    margin: 0px auto;

    img{
      margin-top: 10px;
      margin-left: 20px;
      float: left;
      height: 50px;
    }

    ul{
      margin: 0px;
      padding: 0px;
      margin: 0px auto;
      float: right;
      margin-right: 20px;

      li{
        list-style: none;
        margin-left: 10px;
        font-weight: bold;
        font-size: 1.5em;
        float: left;
        margin-right: 20px;

        a {
          line-height: 70px;
          text-decoration: none;
          color: #999;

          &:hover{
          animation-name: menuHover;
          animation-duration: 1s;
          animation-fill-mode: forwards;
          }

        }
      }
    }
  }
}