::-webkit-scrollbar{
  background:transparent;
  width: 10px;
}
::-webkit-scrollbar-track{
  opacity: 0;

}
::-webkit-scrollbar-thumb{
  background-color: rgba(0,0,0,0.2);
  border-radius: 10px;
  margin: 5px;
}

.clearFloat{
  clear: both;
}
body {
  overflow-x: hidden;
  margin: 0;
  background-color: #fafafa;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}
