.itemBox{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0px auto;
  .readyToUpdate{
    margin: 0 20px 50px 20px;
    width: 100%;
    text-align: center;
    font-weight: 100;
    font-size: 1.5em;
  }
}
.card{
  flex: 1;
  min-width: 380px;
  background-color: white;
  margin: 2px;
  border-radius: 5px;
  text-align: center;
  p{

    font-weight: 500;
    font-size: 2em;
    margin-top: 20px;
  }
  .line{
    width: 30%;
    height: 1px;
    margin: 20px auto;
    background-color: #aaa;
  }
  h3{
    font-weight: 300;
    padding: 0 20% 20px 20%;
  }
}

.itemTitleBox{
  text-align: center;
  margin: 30px;
  font-size: 2em;
  font-weight: 100;
  .itemTitleLine{
    width: 100px;
    height: 1px;
    background-color: #999;
    margin: 10px auto;
  }
}