@keyframes menuHover {
  100%{
    color: #333;
  }
}

@keyframes leftToright {
  0%{
    opacity: 0;
    left: -500px;
  }
  100%{
    opacity: 1;
    left: 0px;
  }
}
@keyframes headerTextGradient{
  0%{color: #000000;}
  10%{color: #ffc155;}
  30%{color: #e1afff;}
  50%{color: #ae4bff;}
  70%{color: #ff6c80;}
  80%{color: #8284ff;}
  100%{color: #000000}
}